<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog">添加分类</el-button>
        </el-col>
      </el-row>
      <el-row>
        <tree-table
          :data="catelist"
          :columns="columns"
          :selection-type="false"
          :expand-type="false"
          :show-index="true"
          index-text="#"
          :border="true"
          :show-row-hover="false"
          class="treeTable"
        >
            <!-- 是否有效列 -->
            <template slot="isok" slot-scope="scope">
                <i style="color: lightgreen" class="el-icon-success" v-if="scope.row.cat_deleted === false"></i>
                <i style="color: red" class="el-icon-error" v-else></i>
            </template>
            <!-- 排序列 -->
            <template slot="order" slot-scope="scope">
                <el-tag v-if="scope.row.cat_level === 0" size="mini">一级</el-tag>
                <el-tag v-else-if="scope.row.cat_level === 1" size="mini" type="success">二级</el-tag>
                <el-tag v-else size="mini" type="warning">三级</el-tag>
            </template>
            <!-- 操作列 -->
            <template slot="opt">
                <el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
            </template>
        </tree-table>
      </el-row>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 添加分类对话框 -->
    <el-dialog title="添加用户" :visible.sync="addCateDialogVisible" width="30%" @close="addCateDialogClosed">
      <!-- 添加分类表单区 -->
      <el-form :model="addCateForm" ref="addCateFormRef" label-width="70px">
        <el-form-item  label="分类名称">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item  label="父级分类">
            <el-cascader size="mini" :collapse-tags="true" v-model="selectedKeys" :options="parentCateList" :props="cascaderProps" @change="parentCateChanged"></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      /* 查询条件 */
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      /* 商品分类的数据列表 */
      catelist: [],
      total: 0,
      /* 为table指定列的定义 */
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name',
        },
        {
            label: '是否有效',
            /* 表示将当前列定义为模板列 */
            type: 'template',
            template: 'isok'
        },
        {
            label: '排序',
            /* 表示将当前列定义为模板列 */
            type: 'template',
            template: 'order'
        },
        {
            label: '操作',
            /* 表示将当前列定义为模板列 */
            type: 'template',
            template: 'opt'
        }
      ],
      /* 控制添加分类对话框的显示与隐藏 */
      addCateDialogVisible: false,
      /* 添加分类的表单数据对象 */
      addCateForm: {
          cat_pid: 0,
          cat_name: '',
          cat_level: 0
      },
      /* 父级分类的列表 */
      parentCateList: [],
      /* 指定级联选择器的配置对象 */
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      /* 选中的父级分类的ID数组 */
      selectedKeys: []
    }
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200) {
        return this.$message.error('查询商品分类失败')
      }
      this.catelist = res.data.result
      this.total = res.data.total
    },
    handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        this.getCateList()
    },
    handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage
        this.getCateList()
    },
    showAddCateDialog() {
        this.getParentCateList()
        this.addCateDialogVisible = true
    },
    /* 获取父级分类的数据列表 */
    async getParentCateList() {
        const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
        if (res.meta.status !== 200) {
            return this.$message.error('获取失败')
        }
        this.parentCateList = res.data
    },
    parentCateChanged() {
        if (this.selectedKeys.length > 0) {
            /* 父级分类的ID */
            this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
            /* 为当前分类的等级赋值 */
            this.addCateForm.cat_level = this.selectedKeys.length
        } else {
            this.addCateForm.cat_pid = 0
            this.addCateForm.cat_level = 0
        }
    },
    /* 添加新的分类 */
    async addCate() {
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
            return this.$message.error('添加分类失败')
        }
        this.$message.success('添加分类成功')
        this.getCateList()
        this.addCateDialogVisible = false
    },
    addCateDialogClosed() {
        this.$refs.addCateFormRef.resetFields()
        this.selectedKeys = []
        this.addCateForm = {
          cat_pid: 0,
          cat_name: '',
          cat_level: 0
        }
    }
  },
  created() {
    this.getCateList()
  },
}
</script>

<style lang="less" scoped>
.treeTable {
    margin-top: 15px;
}
</style>
