<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <el-alert
        title="注意:只允许为第三级分类设置相关参数！"
        type="warning"
        show-icon
        :closable="false"
      ></el-alert>
      <el-row class="cat_opt">
        <span>选择商品分类：</span>
        <el-cascader
          v-model="selectedKeys"
          :options="cateList"
          :props="cateProps"
          @change="handleChange"
        ></el-cascader>
      </el-row>
      <el-row>
        <el-tabs v-model="activeName" @tab-click="handleTabClick" class="cat_opt">
          <el-tab-pane label="动态参数" name="many">
            <el-button size="mini" type="primary" :disabled="this.selectedKeys.length == 3 ? false : true" @click="addDialogVisible = true">添加参数</el-button>
            <el-table :data="manyTableData" border stripe>
                <el-table-column type="expand">
                    <template slot-scope="scope">
                      <div class="vcenter">
                      <el-tag @close="handleClose(i, scope.row)" closable v-for="(item, i) in scope.row.attr_vals" :key="i">{{item}}</el-tag>
                      <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)"></el-input>
                      <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                      </div>
                    </template>
                </el-table-column>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="参数名称" prop="attr_name"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" icon="el-icon-edit" type="primary" @click="showEditDialog(scope.row.attr_id)">修改</el-button>
                        <el-button size="mini" icon="el-icon-delete" type="danger" @click="removeParamsById(scope.row.attr_id)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="静态属性" name="only">
            <el-button size="mini" type="primary" :disabled="this.selectedKeys.length == 3 ? false : true" @click="addDialogVisible = true">添加属性</el-button>
             <el-table :data="onlyTableData" border stripe>
                <el-table-column type="expand">
                    <template slot-scope="scope">
                      <div class="vcenter">
                        <el-tag @close="handleClose(i, scope.row)" closable v-for="(item, i) in scope.row.attr_vals" :key="i">{{item}}</el-tag>
                        <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)"></el-input>
                        <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                      </div>
                    </template>
                </el-table-column>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="属性名称" prop="attr_name"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" icon="el-icon-edit" type="primary" @click="showEditDialog(scope.row.attr_id)">修改</el-button>
                        <el-button size="mini" icon="el-icon-delete" type="danger" @click="removeParamsById(scope.row.attr_id)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </el-row>
    </el-card>
    <!-- 添加对话框 -->
    <el-dialog :title="'添加' + titleText" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <el-form :model="addForm" ref="addFormRef" label-width="100px">
            <el-form-item :label="titleText">
                <el-input v-model="addForm.attr_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addParams">确 定</el-button>
        </span>
    </el-dialog>
    <!-- 修改对话框 -->
    <el-dialog :title="'修改' + titleText" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <el-form :model="editForm" ref="editFormRef" label-width="100px">
            <el-form-item :label="titleText">
                <el-input v-model="editForm.attr_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="editParams">确 定</el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cateList: [],
      cateProps: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id',
        children: 'children',
      },
      selectedKeys: [],
      activeName: 'many',
      /* 获取到的动态参数 */
      manyTableData: [],
      /* 获取到的静态属性 */
      onlyTableData: [],
      addDialogVisible: false,
      addForm: {
          attr_name: ''
      },
      editDialogVisible: false,
      editForm: {
          attr_name: ''
      }
    }
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }
      this.cateList = res.data
    },
    handleChange() {
        this.getParamsData()
    },
    /* tab页签点击事件的处理函数 */
    handleTabClick() {
        this.getParamsData()
    },
    /* 获取参数的列表数据 */
    async getParamsData() {
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName },
        })
      if (res.meta.status !== 200) {
        return this.$message.error('获取参数失败')
      }
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        item.inputVisible = false
        item.inputValue = ''
      })
      if (this.activeName === 'only') {
          this.onlyTableData = res.data
      } else {
          this.manyTableData = res.data
      }
    },
    addDialogClosed() {
        this.addForm.attr_name = ''
    },
    async addParams() {
        const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName
        })
        if (res.meta.status !== 201) {
            return this.$message.error('添加' + this.titleText + '失败')
        }
        this.$message.success('添加' + this.titleText + '成功')
        this.addDialogVisible = false
        this.getParamsData()
    },
    async showEditDialog(id) {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes/${id}`, {
            params: { attr_sel: this.activeName }
        })
        if (res.meta.status !== 200) {
            return this.$message.error('获取' + this.titleText + '失败')
        }
        this.editForm = res.data
        this.editDialogVisible = true
    },
    editDialogClosed() {
        this.editForm.attr_name = ''
    },
    async editParams() {
        const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, {
            attr_name: this.editForm.attr_name,
            attr_sel: this.activeName
        })
        if (res.meta.status !== 200) {
            return this.$message.error('修改' + this.titleText + '失败')
        }
        this.$message.success('修改' + this.titleText + '成功')
        this.getParamsData()
        this.editDialogVisible = false
    },
    async removeParamsById(id) {
      /* 弹框询问用户是否删除数据 */
      const confirmRes = await this.$confirm('此操作将永久删除该角色权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmRes === 'confirm') {
        const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${id}`)
        if (res.meta.status !== 200) {
          this.$message.error('删除' + this.titleText + '失败')
        } else {
          this.$message.success('删除' + this.titleText + '成功')
          this.getParamsData()
        }
      }
    },
    /* 文本框失去焦点或摁下enter键都会触发 */
    handleInputConfirm(obj) {
      if (obj.inputValue.trim().length === 0) {
        obj.inputValue = ''
        obj.inputVisible = false
        return
      }
      obj.attr_vals.push(obj.inputValue.trim())
      obj.inputValue = ''
      obj.inputVisible = false
      this.saveAttrVails(obj)
    },
    showInput(obj) {
      obj.inputVisible = true
      /*
        $nextTick方法的作用
        当页面上的元素被重新渲染后，才会调用该方法内传入的回调函数
        如果此时的focus方法在$nextTick方法外部使用
        由于inputVisible更改为true，但页面上的元素还没被重新渲染，此时捕获不到input标签
        所以会报错
      */
      this.$nextTick(() => {
          this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    async saveAttrVails(obj) {
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${obj.attr_id}`, {
        attr_name: obj.attr_name,
        attr_sel: obj.attr_sel,
        attr_vals: obj.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('修改失败')
      }
      this.$message.success('修改成功')
    },
    /* 删除对应的参数可选项 */
    handleClose(i, obj) {
      obj.attr_vals.splice(i, 1)
      this.saveAttrVails(obj)
    }
  },
  created() {
    this.getCateList()
  },
  computed: {
    cateId() {
      if (this.selectedKeys.length === 3) {
        return this.selectedKeys[2]
      }
      return null
    },
    titleText() {
       return this.activeName === 'many' ? '动态参数' : '静态属性'
    }
  },
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin-top: 15px;
}
.vcenter{
    display: flex;
    justify-content: center;
    span {
      margin: 0px 10px;
    }
}
.input-new-tag {
  width: 120px;
}
</style>
